#products {
  flex-wrap: wrap;
  justify-content: space-between;
  display: flex;
}

.product {
  width: 200px;
  background-color: #d3d3d3;
  border-radius: 0 0 10px 10px;
  margin-bottom: 15px;
  display: block;
}

.product__image {
  width: 100%;
  height: 150%;
  object-fit: contain;
}

.product__info {
  padding: 10px;
}

.product__name {
  color: #cc904e;
  margin: 20px 5px 5px;
  font-family: Barlow Condensed, sans-serif;
  font-size: 1em;
}

.loader {
  width: 60px;
  height: 60px;
  border: 16px solid #f3f3f3;
  border-top-color: #cc904e;
  border-radius: 50%;
  margin-left: auto;
  margin-right: auto;
  animation: spin 2s linear infinite;
  position: absolute;
  top: 50%;
  left: 0;
  right: 0;
  transform: translateY(-50%);
}

@keyframes spin {
  0% {
    transform: rotate(0deg);
  }

  100% {
    transform: rotate(360deg);
  }
}

